<script>
export default {
  name: 'MixUser',
  data () {
    return {
      loadingStatus: { // 
        loadingItem: ''
      },
      // localStorage 
      carData: JSON.parse(localStorage.getItem('carData')) || [],
      cart: { // GET |  api 
        carts: []
      },
      cartLength: 0, // GET | 
      coupon_code: ''
    }
  },
  computed: { // 
    totalPrice () {
      const vm = this
      const price = []
      vm.carData.forEach((item) => {
        price.push(parseInt(item.price * item.qty))
      })
      return price.reduce(function (total, e) {
        return total + e
      }, 0)
    }
  },
  methods: {
    getCarts () { // GET | 
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      vm.$store.dispatch('updateLoading', true)
      vm.$http.get(api).then((res) => {
        vm.cart = res.data.data
        vm.cartLength = vm.cart.carts.length
        console.log('', vm.cart)
        vm.$store.dispatch('updateLoading', false)
      })
    },
    addlocalCarts (product, num = 1) { //  localStorage 
      const vm = this
      const cacheCarID = []
      vm.carData.forEach((item) => {
        cacheCarID.push(item.product_id)
      })
      vm.toastTopEnd(`${product.title} `, 'success')
      if (cacheCarID.indexOf(product.id) === -1) {
        const cartContent = {
          product_id: product.id,
          qty: num,
          title: product.title,
          imageUrl: product.imageUrl,
          unit: product.unit,
          origin_price: product.origin_price,
          price: product.price,
          category: product.category
        }
        vm.carData.push(cartContent)
        localStorage.setItem('carData', JSON.stringify(vm.carData))
      } else {
        let cache = {}
        vm.carData.forEach((item, key) => {
          if (item.product_id === product.id) {
            let { qty } = item
            cache = {
              product_id: product.id,
              qty: qty += num,
              title: product.title,
              imageUrl: product.imageUrl,
              unit: product.unit,
              origin_price: product.origin_price,
              price: product.price,
              category: product.category
            }
            vm.carData.splice(key, 1)
          }
        })
        vm.carData.push(cache)
        localStorage.setItem('carData', JSON.stringify(vm.carData))
      }
    },
    delLocalCart (cart) { //  localStorage "" 
      const vm = this
      vm.carData.filter((item, key) => {
        if (item.product_id === cart.product_id) {
          vm.carData.splice(key, 1)
          localStorage.setItem('carData', JSON.stringify(vm.carData))
          vm.toastTopEnd('', 'success')
        }
      })
    },
    delAllLocalCarts () { //  localStorage  -  Shopping  Cart.vue 
      localStorage.removeItem('carData') //  localStorage 
      //  localStorage 
      this.carData = JSON.parse(localStorage.getItem('carData')) || [] //  localStorage 
      console.log(' localStorage ')
      this.toastTopEnd('', 'success')
    },
    addCouponCode () { //  Coupon
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`
      const coupon = {
        code: vm.coupon_code
      }
      vm.$store.dispatch('updateLoading', true)
      vm.$http.post(api, { data: coupon }).then((res) => {
        if (res.data.success) {
          vm.toastTopEnd(res.data.message, 'success')
          vm.$store.dispatch('updateLoading', false)
          vm.getCarts()
        } else {
          vm.toastTopEnd(res.data.message, 'error')
          vm.$store.dispatch('updateLoading', false)
        }
      })
    },
    //  API  Order 
    postCarts () {
      this.coupon_code = ''
      this.$store.dispatch('updateLoading', true)
      const delApi = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/carts`
      const postapi = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      this.$http.delete(delApi)
        .then(() => { // 1. delete | 
          console.log('1.  API ')
        })
        .then(() => {
          this.carData.forEach((item, index) => { // 2. 
            const cache = {
              product_id: item.product_id,
              qty: item.qty
            }
            this.$http.post(postapi, { data: cache }).then(() => { // 3. POST | 
              console.log('2. ', index, 'POST ')
              setTimeout(() => {
                if ((this.carData.length - 1) === index) {
                  console.log('3.  index', index, '')
                  this.$store.dispatch('updateLoading', false)
                  this.$router.push('/checkout/order_create')
                  this.getCarts()
                }
              }, 4000)
            })
          })
        })
    },
    removeCartItem (id) { // Delete | "" 
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`
      vm.$store.dispatch('updateLoading', true)
      vm.$http.delete(api)
        .then((res) => {
          if (res.data.success) {
            vm.toastTopEnd(res.data.message, 'success')
            vm.$store.dispatch('updateLoading', false)
            vm.getCarts()
          } else {
            vm.toastTopEnd(res.data.message, 'error')
            vm.$store.dispatch('updateLoading', false)
          }
        })
        .catch((err) => {
          vm.$swal({ title: err.data.message, icon: 'error' })
          vm.$store.dispatch('updateLoading', false)
        })
    },
    deleteAllCarts () { // Delete | "" 
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/carts`
      vm.$store.dispatch('updateLoading', true)
      vm.$http.delete(api)
        .then((res) => {
          console.log('', res.data)
          if (res.data.success) {
            vm.toastTopEnd('', 'success')
            vm.getCarts()
          } else {
            vm.toastTopEnd(res.data.message, 'error')
          }
          vm.$store.dispatch('updateLoading', false)
        })
        .catch((err) => {
          vm.$swal({ title: err.data.message, icon: 'error' })
          vm.$store.dispatch('updateLoading', false)
        })
    },
    cartBtn (status, item) { // Cart.vue:  addminus 
      const vm = this
      if (status === 'add') {
        vm.carData.filter((filterItem) => {
          if (filterItem.product_id === item.product_id) {
            filterItem.qty = filterItem.qty + 1
          }
          localStorage.setItem('carData', JSON.stringify(vm.carData))
        })
      } else if (status === 'minus') {
        if (item.qty <= 1) { return }
        vm.carData.filter((filterItem) => {
          if (filterItem.product_id === item.product_id) {
            filterItem.qty = filterItem.qty - 1
          }
          localStorage.setItem('carData', JSON.stringify(vm.carData))
        })
      }
    },
    productCartBtn (status) { // Product.vue:  addminus 
      if (status === 'add') {
        this.num += 1
      } else if (status === 'minus') {
        this.num -= 1
      }
    },
    // : 
    getDiscount (totalPrice) {
      const discount = Math.round(totalPrice)
      return discount
    },
    toastTopEnd (msg, icon) {
      this.$swal({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 3000,
        icon: icon,
        title: msg
      })
    }
  },
  created () {
    //
  }
}
</script>
